import {
  SET_JOBS,
  SET_COURSES,
  SET_LOADER,
  SET_USER_PROFILE,
  SET_PROJECTS,
  SET_EMPLOYEES,
  SET_API_ERROR,
  SET_QUOTE_DETAILS,
} from "./actions";

const initialState = {
  jobsData: {},
  coursesData: {},
  projects: [],
  profile: {},
  employees: {},
  quoteDetails: [],
  loader: false,
  apiError: null,
};

export default function employeePortalStore(state = initialState, action) {
  switch (action.type) {
    case SET_LOADER:
      return {
        ...state,
        loader: action.payload,
      };
    case SET_API_ERROR:
      return {
        ...state,
        apiError: action.payload,
      };
    case SET_USER_PROFILE:
      return {
        ...state,
        profile: action.payload,
      };
    case SET_JOBS:
      return {
        ...state,
        jobsData: action.payload,
      };
    case SET_COURSES:
      return {
        ...state,
        coursesData: action.payload,
      };
    case SET_EMPLOYEES:
      return {
        ...state,
        employees: action.payload,
      };
    case SET_PROJECTS:
      return {
        ...state,
        projects: action.payload,
      };
    case SET_QUOTE_DETAILS:
      return {
        ...state,
        quoteDetails: action.payload,
      };
    default:
      return state;
  }
}
