/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";
import { isEmpty } from "lodash";

import { PropTypes } from "prop-types";
import { useDispatch } from "react-redux";

import { checkSubmitButtonDisability, handleButtonIsDisabled, handleHasError, numOfErrorsInErrorObj } from "../../utils/formUtils";
import {
  jobApplicationConstants,
  ROLE,
  ROLES,
  EMPLOYEE_DETAILS,
  MIN3CHARS_ERROR_TEXT,
  INVALID_DATE,
} from "../../constants";
import { YourReferralText, FieldsWrapper } from "./styles";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields } from "..";
import { createOrUpdatePermanentAddressData, getEmployeesList } from "../../store/actions";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const {
  id,
  projectName,
  projectDesc,
  startDate,
  endDate,
  client,
  clientLocation
} = EMPLOYEE_DETAILS.PROJECTS_FORM_IDS;

const ProjectsApplicationForm = ({onClickCrossIcon, onClickSubmitButton, empData = {} }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [error, setError] = useState({});
  const [data, setData] = useState(empData);

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };
  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  return (
    <FieldsWrapper>
      <CloseOutlinedIcon
          className={classes.root}
          onClick={onClickCrossIcon}
        />
      <YourReferralText>Projects Form</YourReferralText>
      <Fields>
        <TextInput
          id={projectName}
          label="Project name"
          onChange={handleChange}
          error={error[projectName]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[projectName]}
        />
        <TextInput
          id={projectDesc}
          label="Project Description"
          onChange={handleChange}
          error={error[projectDesc]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[projectDesc]}
        />
        <DatePicker
          handleChange={handleChange}
          id={startDate}
          label="Start Date"
          cssClass={classes.datePicker}
          defaultValue={empData.startDate}
          helperText={INVALID_DATE}
        />
         <DatePicker
          handleChange={handleChange}
          id={endDate}
          label="End Date"
          cssClass={classes.datePicker}
          defaultValue={empData.endDate}
          helperText={INVALID_DATE}
        />
        <TextInput
          id={endDate}
          label="City"
          onChange={handleChange}
          error={error[endDate]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[endDate]}
        />
        <TextInput
          id={client}
          label="Client"
          onChange={handleChange}
          error={error[client]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[client]}
        />
        <TextInput
          id={clientLocation}
          label="Client Location"
          onChange={handleChange}
          error={error[clientLocation]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[clientLocation]}
        />
      </Fields>
      <PrimaryButton
        handleClick={() => onClickSubmitButton(data)}
        cssClass={classes.button}
        label="Submit"
        // isDisabled={checkSubmitButtonDisability({
        //   error,
        //   data: empData,
        //   modifiedData: data,
        //   noOfFields: 10,
        // })}
      />
    </FieldsWrapper>
  );
};

ProjectsApplicationForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default ProjectsApplicationForm;
