/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";
import { useDispatch, useSelector } from "react-redux";

import { PropTypes } from "prop-types";

import { handleButtonIsDisabled, handleHasError } from "../../utils/formUtils";
import {
  jobApplicationConstants,
  ROLE,
  ROLES,
  EMPLOYEE_DETAILS,
  UploadTypes,
} from "../../constants";
import { YourReferralText } from "./styles";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields } from "..";

import ProjectSection from "../ProjectSection/ProjectSection";
import {
  getEmployeeProjects,
  getProjectsList,
  assignEmployeeProject,
} from "../../services/ApiService/actions";

// import { getProjectsList } from "../../store/employeeStore/actions";
import { isEmpty } from "lodash";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const {
  id,
  emailId,
  firstName,
  lastName,
  shortName,
  designation,
  personalEmailId,
  phoneNumber,
  dateOfBirth,
  skills,
  startDate,
  endDate,
} = EMPLOYEE_DETAILS.BASIC_INFORMATION_IDS;

const ProjectsAssignedForm = ({
  onClickCrossIcon,
  onClickSubmitButton,
  empData = {},
}) => {
  const classes = useStyles();
  const [error, setError] = useState({});
  const [data, setData] = useState({});
  const [isDisabled, setIsDisabled] = useState(false);
  const [employeeProjects, setEmployeeProjects] = useState([]);
  const [projects, setProjects] = useState([]);

  const dispatch = useDispatch();

  const getProjects = async () => {
    if (!isEmpty(empData)) {
      const empProjects = await getEmployeeProjects({ empId: empData.id });
      setEmployeeProjects(empProjects);
    }
    const projects = await getProjectsList();
    setProjects(projects);
  };

  useEffect(() => {
    getProjects();
  }, []);

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  useEffect(() => {
    setIsDisabled(handleButtonIsDisabled(error, 3));
  }, [error]);

  const handleClick = () => {
    onClickSubmitButton(data);
  };

  const handleAssignProject = async (project) => {
    await assignEmployeeProject({ empId: empData.id, projectId: project.id });
    const empProjects = await getEmployeeProjects({ empId: empData.id });
    setEmployeeProjects(empProjects);
  };

  return (
    <ModalInputsWrapper>
      <CloseOutlinedIcon className={classes.root} onClick={onClickCrossIcon} />
      <YourReferralText>Projects</YourReferralText>
      <Fields>
        <ProjectSection
          employee={empData}
          projects={projects}
          employeeProjects={employeeProjects}
          onClickAssign={handleAssignProject}
        />
      </Fields>
    </ModalInputsWrapper>
  );
};

ProjectsAssignedForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default ProjectsAssignedForm;
