export const downloadEmployeeDocuments = ({ path }) => {
  return api
    .get(apiEndpoints.download_employee_documents({ path }))
    .then((response) => {
      const url = window.URL.createObjectURL(
        new Blob([response.data], { type: "text/jpeg" })
      );
      const a = document.createElement("a");
      a.setAttribute("hidden", "");
      a.setAttribute("href", url);
      a.setAttribute("download", path); //or any other extension
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      return response.data;
    });
};

