import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { Heading, CardWrapper, Actions } from "./styles";
import Modal from "../../../CommonComponents/Modal";
import BasicInformationForm from "../BasicInformationForm";
import EmployeeDocumentsForm from "../EmployeeDocumentsForm";
import ProjectsAssignedForm from "../ProjectAssignedView";
import BankDetailsForm from "../BankDetailsForm";
import AddressForm from "../AddressForm";
import CurrentAddressForm from "../CurrentAddressForm";
import EmergencyContactForm from "../EmergencyContactForm";
import { PrimaryButton } from "../../../CommonComponents";
import {
  createEmployee,
  updateEmployee,
  createOrUpdatePermanentAddress,
  createOrUpdateCurrentAddress,
  createOrUpdateBankDetails,
  updateEmployeeDocumentDetails,
  createOrUpdateEmergencyContact,
  getQuotes,
  createOrUpdatePreviousCompany,
} from "../../services/ApiService/actions";
import { getEmployeesList } from "../../store/employeeStore/actions";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlusCircle } from "@fortawesome/free-solid-svg-icons";

import {
  BASIC_INFO,
  DOCUMENTS,
  PROJECTS_ASSIGNED,
  BANK_DETAILS,
  ADDRESS,
  EMERGENCY_CONTACT,
  formTypes,
  PREVIOUS_COMPANY,
} from "./shared";
import PreviousCompanyForm from "../PreviousCompanyForm";
import PreviousCompanyView from "../PreviousCompanyView";

const EmployeeDetailsView = ({
  employeeData = {},
  handleBackButtonClick = () => {},
  updateSelectedEmployee = () => {},
}) => {
  const [item, setItem] = useState("");
  const dispatch = useDispatch();

  useEffect(() => {
    getQuotes();
  });
  const handleSubmitEmployeeData = async ({ type, data }) => {
    switch (type) {
      case "createEmployee":
        updateSelectedEmployee(data);
        await createEmployee(data);
        break;
      case "updateEmployee":
        updateSelectedEmployee(data);
        await updateEmployee(data);
        break;
      case "createOrUpdateBankDetails":
        updateSelectedEmployee({ ...employeeData, bankDetails: data });
        await createOrUpdateBankDetails({ empId: employeeData.id, body: data });
        break;
      case "createOrUpdatePermanentAddress":
        updateSelectedEmployee({ ...employeeData, permanentAddress: data });
        await createOrUpdatePermanentAddress({
          empId: employeeData.id,
          body: data,
        });
        break;
      case "createOrUpdateCurrentAddress":
        updateSelectedEmployee({ ...employeeData, currentAddress: data });
        await createOrUpdateCurrentAddress({
          empId: employeeData.id,
          body: data,
        });
        break;
      case "updateDocumentDetails":
        await updateEmployeeDocumentDetails({
          empId: employeeData.id,
          body: data,
        });
        break;
      case "EMERGENCY_CONTACT":
        updateSelectedEmployee({ ...employeeData, emergencyContact: data });
        await createOrUpdateEmergencyContact({
          empId: employeeData.id,
          body: data,
        });
        break;
      case "PREVIOUS_COMPANY":
        updateSelectedEmployee({ ...employeeData, previousCompany: data });
        await createOrUpdatePreviousCompany({
          empId: employeeData.id,
          body: data,
        });
        break;
      default:
        break;
    }
    dispatch(getEmployeesList({ page: 0 }));
  };

  const getModalContent = (data) => {
    switch (item) {
      case BASIC_INFO.id:
        return (
          <BasicInformationForm
            empData={employeeData}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={(data) => {
              handleSubmitEmployeeData({ type: "createEmployee", data });
            }}
            onClickUpdateButton={(data) => {
              handleSubmitEmployeeData({ type: "updateEmployee", data });
            }}
          />
        );
      case DOCUMENTS.id:
        return (
          <EmployeeDocumentsForm
            empData={employeeData}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={(data) => {
              handleSubmitEmployeeData({ type: "updateDocumentDetails", data });
            }}
          />
        );
      case PROJECTS_ASSIGNED.id:
        return (
          <ProjectsAssignedForm
            empData={employeeData}
            onClickCrossIcon={() => {
              setItem("");
            }}
          />
        );
      case BANK_DETAILS.id:
        return (
          <BankDetailsForm
            empData={employeeData.bankDetails || {}}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={(data) => {
              handleSubmitEmployeeData({
                type: "createOrUpdateBankDetails",
                data,
              });
            }}
          />
        );
      case ADDRESS.id:
        return (
          <AddressForm
            empData={employeeData}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={({ type, data }) => {
              handleSubmitEmployeeData({ type, data });
            }}
          />
        );
      case PREVIOUS_COMPANY.id:
        return (
          <PreviousCompanyView
          companiesData={employeeData.previousCompany || {}}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={(data) => {
              handleSubmitEmployeeData({ type: "PREVIOUS_COMPANY", data });
            }}
          />
        );
      case EMERGENCY_CONTACT.id:
        return (
          <EmergencyContactForm
            empData={employeeData.emergencyContact || {}}
            onClickCrossIcon={() => {
              setItem("");
            }}
            onClickSubmitButton={(data) => {
              handleSubmitEmployeeData({ type: "EMERGENCY_CONTACT", data });
            }}
          />
        );
      default:
        return null;
    }
  };

  //   const get

  return (
    <div
      style={{
        width: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        padding: "20px 40px",
      }}
    >
      <Modal
        open={item !== ""}
        handleClose={() => {
          setItem("");
        }}
      >
        {getModalContent()}
      </Modal>
      <CardWrapper
        moreStyles={{ bColor: "transparent", padding: "0" }}
        onClick={handleBackButtonClick}
      >
        <PrimaryButton label={"Back"} />
      </CardWrapper>
      <h2
        style={{
          marginLeft: "18px",
          marginBottom: "18px",
          fontFamily: "Roboto",
          textTransform: "none",
          color: "#173b56",
        }}
      >
        Employee details
      </h2>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        {formTypes.map((type) => (
          <CardWrapper key={type.id} onClick={() => setItem(type.id)}>
            <div>
              <Heading>{type.label}</Heading>
            </div>
            <FontAwesomeIcon
              icon={faPlusCircle}
              style={{ fontSize: "24px", color: "#173b56", cursor: "pointer" }}
              onClick={() => setItem(type.id)}
            />
          </CardWrapper>
        ))}
      </div>
    </div>
  );
};

export default EmployeeDetailsView;
