const DashboardRequestQuoteDetails = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [selectedQuote, setSelectedQuote] = useState(null);

  const store = useSelector((state) => get(state, ["employeeStore"], {}));
  //   const quoteDetails = get(store, "quoteDetails", data);
  const quoteDetails = data;

  const loader = get(store, "loader", false);

  useEffect(() => {
    dispatch(getQuoteDetails());
  }, []);

  const resetModal = () => {
    setSelectedQuote(null);
  };

  const onClickCrossIcon = () => {
    setSelectedQuote(null);
  };

  const getModalContent = () => {
    return (
      <QuoteContainer>
        <CloseOutlinedIcon
          className={classes.root}
          onClick={onClickCrossIcon}
        />
        <Heading>Quote details</Heading>
        <Title>Service name</Title>
        <Text>{selectedQuote.serviceName}</Text>
        <hr />
        <Title>Name</Title>
        <Text>{selectedQuote.name}</Text>
        <Title>Email</Title>
        <Text>{selectedQuote.email}</Text>
        <Title>Phone number</Title>
        <Text>{selectedQuote.phoneNumber}</Text>
        <Title>Message</Title>
        <Text>{selectedQuote.message}</Text>
      </QuoteContainer>
    );
  };

  return (
    <Container loader={false}>
      <>
        {selectedQuote && (
          <Modal
            open={!!selectedQuote}
            handleClose={() => {
              resetModal();
            }}
          >
            {getModalContent()}
          </Modal>
        )}
        <div
          style={{
            width: "90%",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            padding: "20px 0",
          }}
        >
          <SearchBarComponent
            onCancelSearch={() => {}}
            onRequestSearch={(val) => {
              console.log(val);
            }}
          />
        </div>
        <Table
          noCount
          page={0}
          data={quoteDetails}
          columnKeys={[
            { id: "serviceName", label: "Service Name" },
            { id: "name", label: "Name" },
            { id: "email", label: "Email" },
            { id: "phoneNumber", label: "Phone number" },
            { id: "message", label: "Message" },
          ]}
          //   rowsPerPage={}
          //   count={}

          onSelectTableRow={(row) => {
            setSelectedQuote(row);
          }}
          gotoNextPage={(page) => {
            // dispatch(getDashboardPageJobsList({ page }));
          }}
        />
      </>
    </Container>
  );
};

