/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";

import { PropTypes } from "prop-types";
import { YourReferralText } from "./styles";
import { ModalInputsWrapper, Fields } from "..";
import ControlledAccordions from "../../../CommonComponents/ControlledAccordions";
import PreviousCompanyForm from "../PreviousCompanyForm";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const PreviousCompanyView = ({
  onClickCrossIcon,
  onClickSubmitButton,
  companiesData = [],
}) => {
  const classes = useStyles();
  // const [companiesData, setCompaniesData] = useState(companies);

  const submitData = ({type ,id , data}) => {
    let finalComp = []
    if(type === 'add') {
     finalComp = [ ...companiesData, data]
    } else if(type === 'delete') {
      finalComp = companiesData.filter((company, i) => i !== id)
    } else if( type === 'update') {
      finalComp = companiesData
      finalComp[id] = data
    }
    onClickSubmitButton(finalComp)
    setTimeout(() =>{
      onClickCrossIcon()
    },1000)
  }

  const getDataArray = () => {
    return companiesData.map((company, i) => {
      return {
        id: `Company-${company?.name}`,
        summary: `Company ${company?.name}`,
        details: (
          <PreviousCompanyForm
            empData={company}
            onClickSubmitButton={(data) => {
              submitData({type: 'update',id:i ,data});
            }}
            onDeleteClick={()=> {
              submitData({id:i ,type: 'delete'});
            }}
            hasDelete
          />
        ),
      };
    });
  };

  return (
    <ModalInputsWrapper>
      <CloseOutlinedIcon className={classes.root} onClick={onClickCrossIcon} />
      <YourReferralText>Previous Company Details</YourReferralText>
      
      <ControlledAccordions dataArray={getDataArray()} />
      <div style={{height: '20px'}} />
      <ControlledAccordions dataArray={[ {
        id: `Company-${0}`,
        summary: `Add company`,
        details: (
          <PreviousCompanyForm
            onClickSubmitButton={(data) => {
              submitData({type: 'add',data});
            }}
          />
        ),
      }]} />
    </ModalInputsWrapper>
  );
};

PreviousCompanyView.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default PreviousCompanyView;
