/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDownload } from "@fortawesome/free-solid-svg-icons";
import { PropTypes } from "prop-types";

import { handleButtonIsDisabled, handleHasError } from "../../utils/formUtils";
import {
  jobApplicationConstants,
  ROLE,
  ROLES,
  EMPLOYEE_DETAILS,
  UploadTypes,
  MIN3CHARS_ERROR_TEXT,
  DROPDOWN_ERROR_TEXT,
} from "../../constants";

import { YourReferralText, DocumentContainer, Text } from "./styles";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
  Dropdown,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields } from "..";

import { downloadEmployeeDocuments } from "../../services/ApiService/actions";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const {
  panNumber,
  aadharNumber,
  passportNumber,
  pfAccountNumber,
  uan,
  panCardPath,
} = EMPLOYEE_DETAILS.DOCUMENTS_IDS;

const { designation } = EMPLOYEE_DETAILS.BASIC_INFORMATION_IDS;

const EmployeeDocumentsForm = ({
  onClickCrossIcon,
  onClickSubmitButton,
  empData = {},
}) => {
  const classes = useStyles();
  const [error, setError] = useState({});
  const [data, setData] = useState({});
  const [isDisabled, setIsDisabled] = useState(false);
  const {
    panNumber,
    panCardPath,
    passportNumber,
    passportPath,
    personalNumber,
    personalNumberPath,
    resumePath,
    photoPath,
  } = empData;
  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  useEffect(() => {
    setIsDisabled(handleButtonIsDisabled(error, 6));
  }, [error]);

  const handleClick = () => {
    // onClickSubmitButton(data);
    const formData = new FormData();
    formData.append("name", data.name);
    formData.append("value", data.value);
    formData.append("file", data.file);
    onClickSubmitButton(formData);
  };

  const handleFileUpload = (file) => {
    setData({ ...data, file: file });
  };

  const downloadFile = async ({ path }) => {
    await downloadEmployeeDocuments({ path });
  };

  const renderDocumentDetails = ({ doc = {} }) => {
    return (
      <DocumentContainer>
        {doc.title ? (
          <Text>{doc.title}</Text>
        ) : (
          <div
            style={{
              margin: "0 12px",
            }}
          ></div>
        )}
        {doc.number ? (
          <Text>{doc.number}</Text>
        ) : (
          <div
            style={{
              margin: "0 12px",
            }}
          ></div>
        )}
        {doc.path ? (
          <FontAwesomeIcon
            icon={faDownload}
            onClick={() => {
              downloadFile({ path: doc.path });
            }}
            style={{
              margin: "0 12px",
              width: "18px",
              height: "18px",
              cursor: "pointer",
            }}
          />
        ) : (
          <div
            style={{
              margin: "0 12px",
            }}
          ></div>
        )}
      </DocumentContainer>
    );
  };

  return (
    <ModalInputsWrapper>
      <CloseOutlinedIcon className={classes.root} onClick={onClickCrossIcon} />
      <YourReferralText>Documents Form</YourReferralText>
      <div
        style={{
          border: "1px solid grey",
          padding: "30px",
          borderRadius: "5px",
          borderStyle: "dashed",
        }}
      >
        <p>Documents</p>
        <div style={{ display: "flex" }}>
          {panNumber &&
            renderDocumentDetails({
              doc: { title: "Pancard", number: panNumber, path: panCardPath },
            })}
          {passportNumber &&
            renderDocumentDetails({
              doc: {
                title: "Passport",
                number: passportNumber,
                path: passportPath,
              },
            })}
          {personalNumber &&
            renderDocumentDetails({
              doc: {
                title: "Personal number",
                number: personalNumber,
                path: personalNumberPath,
              },
            })}
          {resumePath &&
            renderDocumentDetails({
              doc: {
                title: "Resume",
                number: null,
                path: resumePath,
              },
            })}
        </div>
      </div>
      <div
        style={{
          border: "1px solid grey",
          padding: "30px",
          borderRadius: "5px",
          borderStyle: "dashed",
          marginTop: "12px",
        }}
      >
        <Fields>
          <Dropdown
            inputLabel={"File name"}
            name={"name"}
            onChange={handleChange}
            menuItems={[
              "resume",
              "photo",
              "personalnumber",
              "passport",
              "pancard",
            ]}
            cssClass={classes.field}
            // defaultValue={data.type}
            helperText={DROPDOWN_ERROR_TEXT}
          />
          <TextInput
            id={"value"}
            label="Value"
            onChange={handleChange}
            error={error["value"]}
            cssClass={classes.field}
            // defaultValue={empData[uan]}
            helperText={MIN3CHARS_ERROR_TEXT}
          />
          <div>
            <UploadButton
              label="Upload file"
              dir="referral"
              type={UploadTypes.any}
              handleFileUpload={handleFileUpload}
            />
            {data.file && (
              <span style={{ marginLeft: "8px" }}>
                <i>{data.file.name}</i>
              </span>
            )}
          </div>
        </Fields>
        <PrimaryButton
          handleClick={handleClick}
          cssClass={classes.button}
          label="Submit"
          // isDisabled={isDisabled}
        />
      </div>
    </ModalInputsWrapper>
  );
};

EmployeeDocumentsForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default EmployeeDocumentsForm;
