/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";
import { useDispatch } from "react-redux";
import { PropTypes } from "prop-types";
import { isEmpty } from "lodash";

import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import { checkSubmitButtonDisability, handleButtonIsDisabled, handleHasError } from "../../utils/formUtils";
import {
  jobApplicationConstants,
  ROLE,
  ROLES,
  EMPLOYEE_DETAILS,
  UploadTypes,
  MIN3CHARS_ERROR_TEXT,
  PHONENUMBER_ERROR_TEXT,
  INVALID_DATE,
  MIN1CHARS_ERROR_TEXT,
} from "../../constants";
import { YourReferralText, FieldsWrapper, DeleteButton } from "./styles";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields } from "..";
import { getEmployeesList } from "../../store/actions";
import ControlledAccordions from "../../../CommonComponents/ControlledAccordions";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
  delete: {
    color: '#183B56 !important',
     position: 'absolute !important', 
     top: '-40px', 
     right: '0px',
    border: '1px solid #183B56 !important',
  }
});

const {
  street1,
  street2,
  landmark,
  city,
  provinceOrState,
  country,
  postalCode,
  phoneNumber,
  email,
  type,
} = EMPLOYEE_DETAILS.PERMANENT_ADDRESS_IDS;
const {
  name,
  startDate,
  endDate,
} = EMPLOYEE_DETAILS.PREVIOUS_COMPANY_IDS;

const PreviousCompanyForm = ({
  onClickCrossIcon,
  onClickSubmitButton,
  onDeleteClick,
  empData = {},
  hasDelete = false,
}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [error, setError] = useState({});
  const [data, setData] = useState(empData);
  const [show, setShow] = useState(false);
  const address = data.address || {}; 

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    let key = id || name;
    handleError(key, value);
    let finalData = {}
    if(['name', 'startDate', 'endDate'].includes(key)) {
     finalData =  {
        ...data,
        [key]: value,
      }
    } else {
      finalData =  {
        ...data,
        address: { ...data.address, [key] :value },
      }
    }

    setData(finalData);
  };

  return (
    <div style={{ position: "relative" }}>
      {hasDelete && (
        <Button
          className={classes.delete}
          variant="outlined"
          startIcon={<DeleteIcon />}
          onClick={onDeleteClick}
        >
          Delete
        </Button>
      )}
      <FieldsWrapper>
        <Fields>
          <TextInput
            id={name}
            label="Name"
            onChange={handleChange}
            error={error[name]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={empData[name]}
          />
          <TextInput
            id={startDate}
            label="Start Date"
            onChange={handleChange}
            error={error[startDate]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={empData[startDate]}
          />
          <TextInput
            id={endDate}
            label="End Date"
            onChange={handleChange}
            error={error[endDate]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={empData[endDate]}
          />
          <TextInput
            id={street1}
            label="Street 1"
            onChange={handleChange}
            error={error[street1]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address.street1}
          />
          <TextInput
            id={street2}
            label="Street 2"
            onChange={handleChange}
            error={error[street2]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[street2]}
          />
          <TextInput
            id={landmark}
            label="Landmark"
            onChange={handleChange}
            error={error[landmark]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[landmark]}
          />
          <TextInput
            id={city}
            label="city"
            onChange={handleChange}
            error={error[city]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[city]}
          />
          <TextInput
            id={provinceOrState}
            label="Province Or State"
            onChange={handleChange}
            error={error[provinceOrState]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[provinceOrState]}
          />
          <TextInput
            id={country}
            label="Country"
            onChange={handleChange}
            error={error[country]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[country]}
          />
          <TextInput
            id={postalCode}
            label="Postal Code"
            onChange={handleChange}
            error={error[postalCode]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={address[postalCode]}
          />
          <TextInput
            id={phoneNumber}
            label="Phone Number"
            onChange={handleChange}
            error={error[phoneNumber]}
            cssClass={classes.field}
            helperText={PHONENUMBER_ERROR_TEXT}
            defaultValue={address[phoneNumber]}
          />
          <TextInput
            id={email}
            label="Email"
            onChange={handleChange}
            error={error[email]}
            cssClass={classes.field}
            helperText={INVALID_DATE}
            defaultValue={address[email]}
          />
          <TextInput
            id={type}
            label="Type"
            onChange={handleChange}
            error={error[type]}
            helperText={MIN1CHARS_ERROR_TEXT}
            cssClass={classes.field}
            defaultValue={address[type]}
          />
        </Fields>
        <PrimaryButton
          handleClick={() => onClickSubmitButton(data)}
          cssClass={classes.button}
          label="Submit"
          // isDisabled={checkSubmitButtonDisability({
          //   error,
          //   data: empData,
          //   modifiedData: data,
          //   noOfFields: 10,
          // })}
        />
      </FieldsWrapper>
    </div>
  );
};

PreviousCompanyForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default PreviousCompanyForm;
