import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Heading, SubHeading } from './styles'
import { makeStyles } from '@material-ui/core/styles';
import { PropTypes } from 'prop-types';
import colors from '../../EmployeePortal/themes/colors';

const useStyles = makeStyles((theme) => ({
  summery: {
    backgroundColor: '#e5e5e570 !important',
    position: 'relative',
    "& > div": {
      display: 'flex',
      alignItems: 'center'
    }
  },
}));
export default function ControlledAccordions({ dataArray = [] }) {
  const [expanded, setExpanded] = React.useState(false);
  const classes = useStyles();

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div>
      {dataArray.map( data => {
        return   <Accordion expanded={expanded ===  data.id} onChange={handleChange(data.id)}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
          className={`${classes.summery}`}
        >
          <Heading>
            {data.summary}
          </Heading>
          <SubHeading>{}</SubHeading>
        </AccordionSummary>
        <AccordionDetails>
         {data.details}
        </AccordionDetails>
      </Accordion>
      })}
    </div>
  );
}
