import React, { useState, useEffect } from "react";
import { PropTypes } from "prop-types";
import { get } from "lodash";
import { useDispatch } from "react-redux";

import noData from "../../../assets/svgs/no-data.svg";
import close from "../../../assets/svgs/close.svg";
import { FormLoader } from "../../../CommonComponents";
import {
  removeProjectFromEmployee,
  getFilteredProjects,
  addProjectToEmployee,
  replaceEmployee,
} from "../../utils/employeeUtils";

import ProfileTextView from "../ProfileTextView";

import {
  Heading,
  ProjectContainer,
  ProjectName,
  CrossIcon,
  NodataIcon,
} from "./styles";
import { NoteComponent, DropdownWithButton } from "./shared";

const ProjectSection = (props) => {
  const dispatch = useDispatch();
  const [loader, setLoader] = useState(false);
  const { employee, projects, employeeProjects, onClickAssign } = props;

  const renderProjects = () => {
    if (employeeProjects.length) {
      return employeeProjects.map((project) => (
        <ProjectContainer key={project.id}>
          <ProfileTextView
            text={project.projectName.substring(0, 1)}
            isCircle
            style={{ height: "40px", width: "40px" }}
          />
          <ProjectName>{project.projectName}</ProjectName>
          <CrossIcon src={close} alt="close-icon" onClick={() => {}} />
        </ProjectContainer>
      ));
    }
    return (
      <div
        style={{ display: "flex", justifyContent: "center", marginTop: "40px" }}
      >
        <NodataIcon src={noData} alt="no-data" />
      </div>
    );
  };

  const getLabelValues = () => {
    const filteredProjects = getFilteredProjects({
      allProjects: projects,
      projects: employeeProjects,
    });
    return filteredProjects.map((project) => ({
      label: project.projectName,
      value: project,
    }));
  };

  return (
    <div>
      {/* {loader && <FormLoader />} */}
      {/* <NoteComponent text="In this section, you can assign/unassign a project to employee." /> */}
      <DropdownWithButton
        label="Select a project"
        name="projects"
        onClickAssign={({ value }) => onClickAssign(value)}
        labelValueItems={getLabelValues()}
      />
      {/* <Heading>Assigned Projects</Heading> */}
      {renderProjects()}
    </div>
  );
};

ProjectSection.propTypes = {
  employee: PropTypes.object.isRequired,
};

export default ProjectSection;
