/* eslint-disable react/prop-types */
/* eslint-disable import/prefer-default-export */
import React, { useEffect, useState } from "react";
import { isEmpty } from "lodash";
import { makeStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";

import { PrimaryButton } from "../../../CommonComponents";
import colors from "../../themes/colors";

import { Note, DropdownContainer } from "./styles";

export const NoteComponent = ({ text }) => {
  return <Note>{text}</Note>;
};

const useStyles = makeStyles({
  auto: {
    width: "540px",
    marginRight: "40px",
    "& .MuiFormControl-root": {
      background: `${colors.white} `,
    },
    "& .MuiInputLabel-root": {
      color: `${colors.primary} !important`,
      marginLeft: `10px !important`,
      marginBottom: `10px !important`,
    },
    "& .MuiInput-underline:after": {
      borderBottom: `2px solid ${colors.primary} !important`,
    },
    "& .MuiAutocomplete-input:first-child": {
      padding: `10px 10px !important`,
    },
    "& .MuiAutocomplete-clearIndicator": {
      display: "none",
    },
  },
});

export const DropdownWithButton = ({
  label,
  name,
  onClickAssign,
  labelValueItems = [],
}) => {
  const classes = useStyles();
  const [option, setOption] = useState(null);

  useEffect(() => {
    setOption(null);
  }, []);

  return (
    <DropdownContainer>
      <Autocomplete
        id={name}
        options={labelValueItems}
        getOptionLabel={(o) => o.label}
        renderInput={(params) => (
          <TextField {...params} label={label} className={classes.field} />
        )}
        onChange={(e, value) => {
          setOption(value);
        }}
        className={classes.auto}
        value={option || ""}
      />
      <PrimaryButton
        isDisabled={isEmpty(option)}
        handleClick={() => {
          onClickAssign(option);
          setOption(null);
        }}
        label="Assign"
      />
    </DropdownContainer>
  );
};
